.center {
  display: flex;
  width: 100vw;
  height: 100vh;
}

.center__text {
  text-align: center;
  margin: auto;
  font-family: 'Montserrat', sans-serif;
  font-size: 40px;
  line-height: 55px;
  text-transform: uppercase;
}

.glitch {
  position: relative;  
  color: rgb(255, 123, 0);
  
  mix-blend-mode: lighten;
  &:before {
    text-shadow: 4px 0 rgba(red,0.7);
    animation: glitch-loop-1 1.0s infinite ease-in-out alternate-reverse;
  }
  &:after {
    text-shadow: -4px 0 rgba(blue,0.7);
    animation: glitch-loop-2 1.0s infinite ease-in-out alternate-reverse;
  }

  &:before,
  &:after {
      content: attr(data-text);
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      clip: rect(0, 0, 0, 0);
  }
  
  &:before {
      left: -1px;
      text-shadow: 1px 0 rgba(red,0.7);
  }

  &:after {
      left: 1px;
      text-shadow: -1px 0 rgba(blue,0.7);
  }
}

@keyframes glitch-loop-1 {
  0% { clip: rect(36px, 9999px, 9px, 0) }
  25% { clip: rect(25px, 9999px, 99px, 0) }
  50% { clip: rect(50px, 9999px, 102px, 0) }
  75% { clip: rect(30px, 9999px, 92px, 0) }
  100% { clip: rect(91px, 9999px, 98px, 0) }
}

@keyframes glitch-loop-2 {
  0% { top: -1px; left: 1px; clip: rect(65px, 9999px, 119px, 0) }
  25% { top: -6px; left: 4px; clip: rect(79px, 9999px, 19px, 0) }
  50% { top: -3px; left: 2px; clip: rect(68px, 9999px, 11px, 0) }
  75% { top: 0px; left: -4px; clip: rect(95px, 9999px, 53px, 0) }
  100% { top: -1px; left: -1px; clip: rect(31px, 9999px, 149px, 0) }
}
.wrapper{
    display:block;
    position: sticky;
    top: 0;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    margin:0px;
    text-align:center;
    color:rgb(255, 123, 0);
    font-family:'Roboto';
    font-size:50px;
    font-weight:700;
    overflow:hidden;
    animation:bg 10s linear infinite;
}
@media screen and (min-width: 768px) {
  .center__text {
    font-size: 45px;
  }
}
@media screen and (min-width: 1280px) {
  .center__text {
    font-size: 105px;
    line-height: 105px;
    
  }
}